import React from 'react'
import { Button, Modal } from 'leek-uikit'
import { AutoColumn } from 'components/Transaction/Column'
import LoadingContent from "components/Transaction/LoadingContent"
import { usePostInfo } from "../api"
import ErrorMessage from "../General/ErrorMessage"
import SuccessMessage from "../General/SuccessMessage"
import store from "../store/store"

type ParticipationProps = {
    onDismiss: () => void
}

const SubmissionModal = ({ onDismiss }: ParticipationProps) => {
    const { formData } = store.getState()
    const data = usePostInfo(formData);

    let comp;
    if (data) {
        if (data.error) {
            comp = <ErrorMessage errorMessage={data.error} />
        } else {
            comp = <SuccessMessage successMessage="Thank you for your submission! Our staff will contact you later." />
        }
    } else {
        comp = <LoadingContent />
    }

    return (
        <Modal title="Form Submission" onDismiss={onDismiss}>
            <div>{comp}</div>
            <AutoColumn gap="8px" justify="center">
                <Button onClick={onDismiss} mt="20px">
                    Close
                </Button>
            </AutoColumn>
        </Modal>
    )
}

export default SubmissionModal