interface FormData {
    walletAddress: string;
    username: string;
    country: string;
    postalAddress: string;
    contactNo: string;
    discordName: string;
    email: string;
    isVerified: boolean;
}

const formData: FormData = {
    walletAddress: "",
    username: "",
    country: "",
    postalAddress: "",
    contactNo: "",
    discordName: "",
    email: "",
    isVerified: false,
}

const initialState = {
    formData,
}

export const POST_INFO_DATA = 'POST_INFO_DATA'

export const reducer = (state = initialState, action) => {
    if (action.type === POST_INFO_DATA) {
        const newState = JSON.parse(JSON.stringify(state))
        newState.formData = action.formData
        return newState
    }
    return state
}