import React, { useState, useEffect } from "react"
import styled from "styled-components"
import ReCAPTCHA from "react-google-recaptcha";
import { Card, Flex, CardHeader, Heading, Input, Text, Button, useModal } from "leek-uikit"
import Select, { OptionProps } from "components/Select/select"
import UnlockButton from "components/UnlockButton"
import { useWallet } from "@binance-chain/bsc-use-wallet"
import countries from "config/constants/country"
import validator from 'validator'
import PhoneNumber from "awesome-phonenumber"
import { postcodeValidator, postcodeValidatorExistsForCountry } from 'postcode-validator';
import { POST_INFO_DATA } from "./store/reducer";
import store from "./store/store"
import ConfirmationModal from "./Modal/ConfirmationModal"

interface Props {
    balance: number
    minTokenQty:number
}

const SECRET_TOKEN = process.env.REACT_APP_RECAPTCHA_TOKEN

const SpaceCatsContainer = styled.div`
  background-repeat: no-repeat;
  background-size: contain;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  width: 100%;
  margin-top:30px;
  margin-bottom:30px;
`
const FormLayout = styled.form`
    padding:30px;

    @media (max-width: 768px) {
        padding:20px
    }
`

const NameLayout = styled.div`
  display: grid;
  grid-template-columns: 1fr;

  ${({ theme }) => theme.mediaQueries.sm} {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
`

const AddressLayout = styled.div`
  display: grid;
  grid-template-columns: 1fr;

  ${({ theme }) => theme.mediaQueries.sm} {
    grid-template-columns: 1fr 2fr 1fr;
    grid-gap: 15px;
  }
`

const CountryImg = styled.img`
  width:30px;
  height:30px;
  margin-right:10px;

`


const SpaceCatsInfo: React.FC<Props> = ({ balance,minTokenQty }) => {
    const { account } = useWallet()
    const [country, setCountry] = useState("China")
    const [state, setState] = useState("Anhui")
    const [firstName, setFirstName] = useState('')
    const [lastName, setLastName] = useState('')
    const [streetAddress, setStreetAddress] = useState("")
    const [suburb, setSuburb] = useState("")
    const [countryCode, setCountryCode] = useState("CN");
    const [dialCode, setDialCode] = useState("+86")
    const [postcode, setPostCode] = useState("")
    const [contactNo, setContactNo] = useState("")
    const [discordName, setDiscordName] = useState("")
    const [email, setEmail] = useState("")
    const [validFirstName, setValidFirstName] = useState(true)
    const [validLastName, setValidLastName] = useState(true)
    const [validStreetAddress, setValidStreetAddress] = useState(true)
    const [validSuburb, setValidSuburb] = useState(true)
    const [validPostCode, setValidPostCode] = useState(true)
    const [validContactNo, setValidContactNo] = useState(true)
    const [validDiscordName, setValidDiscordName] = useState(true)
    const [validEmail, setValidEmail] = useState(true)
    const [isVerified, setIsVerified] = useState(false)
    const [countryOptions,setCountryOptions] = useState([])
    const [stateOptions,setStateOptions] = useState([])


    const isAddressNull = account === null
    const isBalanceEnough = balance >= minTokenQty
    const isButtonDisabled = isAddressNull || !validFirstName || !validLastName || !validStreetAddress || !validSuburb || !validPostCode || !validContactNo || !isVerified || !validDiscordName || !validEmail || !isBalanceEnough
  
    useEffect(() => {
        const selectedCountry = countries.find(item => { return item.name === country })
        const allCountries = [];
        const allStates = [];
        setCountryCode(selectedCountry.countryCode.toLowerCase())
        setDialCode(`+${selectedCountry.dialCode}`)

        countries.forEach(item => {
            allCountries.push({
                label: item.name,
                value: item.name
            })
        })

        selectedCountry.state.forEach(item=>{
            allStates.push({
                label: item.name,
                value: item.stateCode,
            })
        })

        setCountryOptions(allCountries)
        setStateOptions(allStates)

    }, [country])

    const [onPresentConfirmationModal] = useModal(<ConfirmationModal onDismiss={() => { return null }} />)

    const validateAllFields = (field: string, fieldValue: string) => {
        if (field === 'firstName') {
            setFirstName(fieldValue)
            if (validator.isLength(fieldValue, { min: 1, max: 30 })) {
                setValidFirstName(true)
            } else {
                setValidFirstName(false)
            }
        } else if (field === 'lastName') {
            setLastName(fieldValue)
            if (validator.isLength(fieldValue, { min: 1, max: 30 })) {
                setValidLastName(true)
            } else {
                setValidLastName(false)
            }
        } else if (field === 'streetAddress') {
            setStreetAddress(fieldValue)
            if (validator.isLength(fieldValue, { min: 1, max: 200 })) {
                setValidStreetAddress(true)
            } else {
                setValidStreetAddress(false)
            }
        } else if (field === 'suburb') {
            setSuburb(fieldValue)
            if (validator.isLength(fieldValue, { min: 1, max: 30 })) {
                setValidSuburb(true)
            } else {
                setValidSuburb(false)
            }
        } else if (field === 'postcode') {
            setPostCode(fieldValue)
            if (postcodeValidatorExistsForCountry(countryCode.toUpperCase()) && postcodeValidator(fieldValue, countryCode.toUpperCase())) {
                setValidPostCode(true)
            } else {
                setValidPostCode(false)
            }
        } else if (field === 'contactNo') {
            setContactNo(fieldValue)
            const pn = new PhoneNumber(contactNo, countryCode.toUpperCase());
            if (pn.isMobile()) {
                setValidContactNo(true)
            } else {
                setValidContactNo(false)
            }
        } else if (field === 'discordName') {
            setDiscordName(fieldValue)
            if (validator.isLength(fieldValue, { min: 1, max: 30 })) {
                setValidDiscordName(true)
            } else {
                setValidDiscordName(false)
            }
        } else if (field === 'email') {
            setEmail(fieldValue)
            if (validator.isEmail(fieldValue)) {
                setValidEmail(true)
            } else {
                setValidEmail(false)
            }
        }
    }

    const handleIsValid = (e, field: string) => {
        validateAllFields(field, e.currentTarget.value);
    }

    const onBlurValidate = (field: string) => {
        const fieldMapping = {
            'firstName': firstName,
            'lastName': lastName,
            "streetAddress": streetAddress,
            "suburb": suburb,
            "postcode": postcode,
            "contactNo": contactNo,
            "discordName": discordName,
            "email": email
        }
        validateAllFields(field, fieldMapping[field])
    }


    const handleCountryChange = (option: OptionProps): void => {
        setCountry(option.value)
    }

    const handleStateChange = (option: OptionProps): void => {
        setState(option.value)
    }

    const onChange = (value) => {
        if (value) {
            setIsVerified(true)
        } else {
            setIsVerified(false)
        }

    }

    const submitInfo = (event) => {
        event.preventDefault()
        const submittedData = {
            walletAddress: account,
            username: `${firstName} ${lastName}`,
            country,
            postalAddress: `${streetAddress}, ${suburb} ${state} ${postcode}`,
            contactNo: `${dialCode} ${contactNo}`,
            discordName,
            email,
            isVerified,
        }
        const action = {
            type: POST_INFO_DATA,
            formData: submittedData
        }
        store.dispatch(action)
        onPresentConfirmationModal()
    }

    return (
        <SpaceCatsContainer>
            <Card>
                <CardHeader>
                    <Flex alignItems="center" justifyContent="space-between">
                        <Heading>SpaceCats Postal Form</Heading>
                    </Flex>
                </CardHeader>

                <FormLayout onSubmit={submitInfo}>
                    <div style={{ marginBottom: '20px' }}>
                        <Text mb="5px">* Country Or Region</Text>
                        <Select options={countryOptions} onChange={handleCountryChange} />
                    </div>

                    <NameLayout>
                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* First Name</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={firstName}
                                    required
                                    placeholder="First Name..."
                                    isSuccess={validFirstName}
                                    isWarning={!validFirstName}
                                    name="firstName"
                                    onChange={(e) => handleIsValid(e, 'firstName')}
                                    onBlur={() => onBlurValidate('firstName')} />
                            </div>
                            <div>
                                {!isAddressNull && !validFirstName && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your first name is empty
                                    </Text>
                                )}

                            </div>
                        </div>

                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* Last Name</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={lastName}
                                    required
                                    placeholder="Last Name..."
                                    isSuccess={validLastName}
                                    isWarning={!validLastName}
                                    name="lastName"
                                    onChange={(e) => handleIsValid(e, 'lastName')}
                                    onBlur={() => onBlurValidate('lastName')} />
                            </div>

                            <div>
                                {!isAddressNull && !validLastName && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your last name is empty
                                    </Text>
                                )}

                            </div>
                        </div>
                    </NameLayout>

                    <div style={{ marginBottom: '20px' }}>
                        <div>
                            <Text mb="5px">* Street Address</Text>
                            <Input type="text"
                                disabled={isAddressNull}
                                value={streetAddress}
                                required
                                placeholder="Street Address..."
                                isSuccess={validStreetAddress}
                                isWarning={!validStreetAddress}
                                name="streetAddress"
                                onChange={(e) => handleIsValid(e, 'streetAddress')}
                                onBlur={() => onBlurValidate('streetAddress')} />
                        </div>
                        <div>
                            {!isAddressNull && !validStreetAddress && (
                                <Text color="failure" fontSize="15px" mt="5px">
                                    Sorry! your street address is empty
                                </Text>
                            )}

                        </div>
                    </div>

                    <AddressLayout>
                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* Suburb / District</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={suburb}
                                    required
                                    placeholder="Suburb..."
                                    isSuccess={validSuburb}
                                    isWarning={!validSuburb}
                                    name="suburb"
                                    onChange={(e) => handleIsValid(e, 'suburb')}
                                    onBlur={() => onBlurValidate('suburb')} />
                            </div>
                            <div>
                                {!isAddressNull && !validSuburb && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your suburb is empty
                                    </Text>
                                )}

                            </div>
                        </div>

                        <div style={{ marginBottom: '20px' }}>
                            <Text mb="5px">* State / Province / Region</Text>
                            <Select options={stateOptions} onChange={handleStateChange} />
                        </div>

                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* Postcode</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={postcode}
                                    required
                                    placeholder="Postcode..."
                                    isSuccess={validPostCode}
                                    isWarning={!validPostCode}
                                    name="postcode"
                                    onChange={(e) => handleIsValid(e, 'postcode')}
                                    onBlur={() => onBlurValidate('postcode')} />
                            </div>
                            <div>
                                {!isAddressNull && !validPostCode && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your postcode is invalid
                                    </Text>
                                )}

                            </div>
                        </div>
                    </AddressLayout>

                    <AddressLayout>
                        <div style={{ marginBottom: '20px' }}>
                            <Text mb="5px">* Country or Region Code</Text>
                            <Flex alignItems="center" justifyContent="space-between">
                                <CountryImg src={`https://www.countryflags.io/${countryCode}/flat/64.png`} alt="country" />
                                <Input
                                    type="text"
                                    value={dialCode}
                                    readOnly
                                    required
                                    isSuccess={account && account.length > 0}
                                    name="walletAddress"
                                />
                            </Flex>
                        </div>

                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* Mobile Phone</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={contactNo}
                                    required
                                    placeholder="Mobile Number..."
                                    isSuccess={validContactNo}
                                    isWarning={!validContactNo}
                                    name="contactNo"
                                    onChange={(e) => handleIsValid(e, 'contactNo')}
                                    onBlur={() => onBlurValidate('contactNo')} />
                            </div>
                            <div>
                                {!isAddressNull && !validContactNo && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your mobile phone is invalid
                                    </Text>
                                )}

                            </div>
                        </div>
                    </AddressLayout>

                    <NameLayout>
                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">* Email Address</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={email}
                                    placeholder="Email Address..."
                                    isSuccess={validEmail}
                                    isWarning={!validEmail}
                                    required
                                    name="email"
                                    onChange={(e) => handleIsValid(e, 'email')}
                                    onBlur={() => onBlurValidate('email')} />
                            </div>
                            <div>
                                {!isAddressNull && !validEmail && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your email is invalid
                                    </Text>
                                )}

                            </div>
                        </div>

                        <div style={{ marginBottom: '20px' }}>
                            <div>
                                <Text mb="5px">Discord Username</Text>
                                <Input type="text"
                                    disabled={isAddressNull}
                                    value={discordName}
                                    placeholder="Discord Username..."
                                    isSuccess={validDiscordName}
                                    isWarning={!validDiscordName}
                                    name="discordName"
                                    onChange={(e) => handleIsValid(e, 'discordName')}
                                    onBlur={() => onBlurValidate('discordName')} />
                            </div>
                            <div>
                                {!isAddressNull && !validDiscordName && (
                                    <Text color="failure" fontSize="15px" mt="5px">
                                        Sorry! your discord name is empty
                                    </Text>
                                )}
                            </div>
                        </div>



                    </NameLayout>

                    <ReCAPTCHA sitekey={SECRET_TOKEN} onChange={onChange} />

                    <div style={{ marginTop: "10px" }}>
                        {
                            account ? <Button fullWidth type="submit" disabled={isButtonDisabled}>Submit</Button> : <UnlockButton fullWidth />
                        }
                    </div>
                </FormLayout>
            </Card>

        </SpaceCatsContainer>
    )
}

export default SpaceCatsInfo
