import React from 'react'
import { Button, Modal, Link, Text, Flex } from 'leek-uikit'
import { AutoColumn } from 'components/Transaction/Column'
import LoadingContent from "components/Transaction/LoadingContent"
import ErrorMessage from "../General/ErrorMessage"
import SuccessMessage from "../General/SuccessMessage"

type ParticipationProps = {
    onDismiss: () => void,
    minTokenQty: number, 
    balance: number
}

const ValidationModal = ({ onDismiss, minTokenQty,balance }: ParticipationProps) => {
    let comp;
    if (balance) {
        if (balance < minTokenQty) {
            const errorMessage = `Sorry! You must hold more than ${minTokenQty} SpaceCats`
            comp = <div>
                <ErrorMessage errorMessage={errorMessage} />
                <Flex alignItems="center" justifyContent="center">
                    <Link href="https://opensea.io/collection/spacecats" target="_blank">
                        <img src="https://testnets.opensea.io/static/images/logos/opensea.svg" alt="opensea" style={{ width: "30px", marginRight: "10px" }} />
                        <Text>Buy on OpenSea</Text>
                    </Link>
                </Flex>

            </div>
        } else {
            comp = <SuccessMessage successMessage="Congratulations! You are qualified to claim our gifts. Please continue to fill in the form" />
        }
    } else {
        comp = <LoadingContent />
    }

    return (
        <Modal title="Validation" onDismiss={onDismiss}>
            <div>{comp}</div>
            <AutoColumn gap="8px" justify="center">
                <Button onClick={onDismiss} mt="20px">
                    Close
                </Button>
            </AutoColumn>
        </Modal>
    )
}

export default ValidationModal