import React from 'react'
import { Button, Modal, Flex, Box, Heading, Text, LinkExternal, useModal } from 'leek-uikit'
import truncateWalletAddress from "utils/truncateWalletAddress"
import { getChainExplorerUrl } from "utils/chainExplorer"
import store from "../store/store"
import SubmissionModal from './SubmissionModal'

const chainId = process.env.REACT_APP_CHAIN_ID

type ParticipationProps = {
    onDismiss: () => void
}

const ConfirmationModal = ({ onDismiss }: ParticipationProps) => {
    const { formData } = store.getState()
    const { username, country, postalAddress, contactNo, walletAddress, email, discordName } = formData
    const [onPresentSubmissionModal] = useModal(<SubmissionModal onDismiss={() => { return null }} />)

    return (
        <Modal title="Information Confirmation" onDismiss={onDismiss}>
            <Box>
                <Text bold color="secondary" style={{ width: "350px" }} mb="10px">* Important: Please double check your details before you sumbit this form.</Text>
                <div style={{ marginBottom: "10px" }}>
                    <Heading size="lg" color="primary">Details:</Heading>
                </div>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Name: </Text>
                    <Text>{username}</Text>
                </Flex>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Country: </Text>
                    <Text>{country}</Text>
                </Flex>

                <Flex alignItems="start" justifyContent="start">
                    <Text bold marginRight="10px">Postal Address: </Text>
                    <Text style={{ width: "250px" }}>{postalAddress}</Text>
                </Flex>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Contact Number: </Text>
                    <Text>{contactNo}</Text>
                </Flex>

                <Flex alignItems="center" justifyContent="start">
                    <Text bold marginRight="10px">Wallet Address: </Text>
                    <LinkExternal href={getChainExplorerUrl(chainId, walletAddress)}>{truncateWalletAddress(walletAddress)}</LinkExternal>
                </Flex>

                {
                    discordName.length > 0 && <Flex alignItems="center" justifyContent="start">
                        <Text bold marginRight="10px">Discord Name: </Text>
                        <Text>{discordName}</Text>
                    </Flex>
                }

                {
                    email.length > 0 && <Flex alignItems="center" justifyContent="start">
                        <Text bold marginRight="10px">Email: </Text>
                        <Text>{email}</Text>
                    </Flex>
                }
            </Box>

            <Flex alignItems="center" justifyContent="space-between" mt="20px">
                <Button onClick={onPresentSubmissionModal}>Submit</Button>
                <Button variant="secondary" onClick={onDismiss}>Cancel</Button>
            </Flex>
        </Modal>
    )
}

export default ConfirmationModal