import React from "react"
import styled from "styled-components"
import Container from "components/layout/Container"
import { useWallet } from "@binance-chain/bsc-use-wallet"
import { Heading } from "leek-uikit"
import Hero from "./Hero"
import { useGetNftBalance, useGetMinmumToken } from "./api"
import SpaceCatsCard from "./SpaceCatsCard"
import SpaceCatsInfo from "./SpaceCatsInfo"

const Title = styled(Heading).attrs({ as: 'h2', size: 'lg' })`
  color: ${({ theme }) => theme.colors.secondary};
  margin-bottom: 24px;
  margin-top:20px;
`

const SpaceCats = () => {
    const { account } = useWallet();
    const balance = useGetNftBalance(account);
    const minTokenQty = useGetMinmumToken();

    return (
        <div>
            <Hero />
            <Container>
                <Title>Step 1: Validation</Title>
                <SpaceCatsCard minTokenQty={minTokenQty} balance={balance}/>
                <Title>Step 2: Fill in the Form</Title>
                <SpaceCatsInfo balance={balance} minTokenQty={minTokenQty}/>
            </Container>
        </div>
    )
}

export default SpaceCats
