import { useState, useEffect } from "react"
import axios from "axios"
import { useERC721 } from "hooks/useContract";
import { APP_VARIABLE } from "config";

const baseUrl = process.env.REACT_APP_API_URL;
const username = process.env.REACT_APP_API_USERNAME
const password = process.env.REACT_APP_API_PASSWORD

export const useGetMinmumToken = () => {
    const [minTokenQty, setMinTokenQty] = useState(0);
    const customUrl = `${baseUrl}/config`;

    useEffect(() => {
        let mounted = true
        const fetchData = async () => {
            try {
                const { data } = await axios.get(customUrl, {
                    params: {
                        variable: APP_VARIABLE.spaceCatsMinQty
                    }
                })
                if (mounted) {
                    setMinTokenQty(Number(data.value))
                }
            } catch (error) {
                console.error('Unable to get minmum token quantity:', error.response)
            }
        }
        fetchData()
        return () => {
            mounted = false
        }
    }, [])// eslint-disable-line react-hooks/exhaustive-deps

    return minTokenQty
}

export const usePostInfo = (formData) => {
    const [successData, setSuccessData] = useState(null);
    const customUrl = `${baseUrl}/spacecats`;

    useEffect(() => {
        let mounted = true
        const fetchData = async () => {
            try {
                const { data } = await axios.post(customUrl,
                    formData,
                    {
                        auth: {
                            username,
                            password
                        }
                    }
                )
                if (mounted) {
                    setSuccessData(data)
                }
            } catch (error) {
                console.error('Unable to post personal info data:', error.response)
            }
        }
        fetchData()
        return () => {
            mounted = false
        }
    }, [customUrl, formData, setSuccessData])

    return successData
}

export const useGetNftBalance = (account) => {
    const [balance, setBalance] = useState<undefined | number>()
    const contract = useERC721()

    useEffect(() => {
        let mounted = true;
        const fetchData = async () => {
            try {
                if (account) {
                    const data = await contract.methods.balanceOf(account).call()

                    if (mounted) {
                        setBalance(data)
                    }
                }
            } catch (err) {
                console.error("Unable to fetch nft balance", err.response)
            }
        }
        fetchData();
        return () => { mounted = false }
    }, [account, contract.methods])
    return balance
}

