import React from "react"
import styled from "styled-components"
import { Card, Flex, CardHeader, Heading, Input, Text, Button, useModal } from "leek-uikit"
import UnlockButton from "components/UnlockButton"
import { useWallet } from "@binance-chain/bsc-use-wallet"
import ValidationModal from "./Modal/ValidationModal"

type Props = {
    minTokenQty: number
    balance:number
}

const SpaceCatsContainer = styled.div`
  background-repeat: no-repeat;
  background-size: contain;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
  width: 100%;
  margin-top:30px;
  margin-bottom:30px;
`
const FormLayout = styled.form`
    padding:30px;

    @media (max-width: 768px) {
        padding:20px
    }
`



const SpaceCatsCard: React.FC<Props> = ({minTokenQty,balance}) => {
    const { account } = useWallet()
    const isAddressNull = account === null

    const [onPresentValidationModal] = useModal(<ValidationModal onDismiss={() => { return null }} minTokenQty={minTokenQty} balance={balance}/>)

    return (
        <SpaceCatsContainer>
            <Card>
                <CardHeader>
                    <Flex alignItems="center" justifyContent="space-between">
                        <Heading>SpaceCats Validation Form</Heading>
                    </Flex>
                </CardHeader>

                <FormLayout>

                    <div style={{ marginBottom: '20px' }}>
                        <Text mb="5px">* What is your Wallet Address?</Text>
                        <Input
                            type="text"
                            disabled={isAddressNull}
                            value={account || ''}
                            readOnly
                            required
                            placeholder="Your wallet address..."
                            isSuccess={account && account.length > 0}
                            name="walletAddress"
                        />
                    </div>
                    {
                        account ? <Button fullWidth onClick={onPresentValidationModal}>Validation</Button> : <UnlockButton fullWidth />
                    }



                </FormLayout>

            </Card>

        </SpaceCatsContainer>
    )
}

export default SpaceCatsCard
